@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
h2 {
  color: white;
  background-color: black;
  position: relative;
  width: 68%;
}
.img {
  display: block;
  outline: none;
}
.btn {
  margin-top: 10px;
  display: block;
  color: #fff;
  background-color: #337ab7;
  border-color: #2e6da4;
  font-weight: 400;
  text-align: center;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.5;
  border-radius: 4px;
  font-size: 20px;
  width: 230px;
}
.btn-red {
  margin-top: 10px;
  display: block;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  padding: 6px 12px;
  font-size: 20px;
  width: 230px;
  line-height: 1.5;
  border-radius: 4px;
  color: #fff;
  background-color: #d9534f;
  border-color: #d43f3a;
  position: absolute;
}
